import React from "react";
import {CircleProgress} from 'react-gradient-progress'

export default function Skills(){

    return (
        <div className="skills-page h-100">
            <p className="txt-lg text-center skill-title">My Current Skill Levels</p>
            <p className="txt-md text-center">Front End Skills</p>
            <div className="skill-level-front">
                <div className="skill-type flex-col">
                    <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={95} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">JavaScript</p>
                    <p className="lead">I have set my skill level to 95 due to the fact that there's always something new to learn!</p>
                    <small>I have been working with <strong>JavaScript</strong> for a over a year now and in that time I have extended my skills from Front End Development to Back End</small>
                </div>

                <div className="skill-type flex-col">
                    <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={95} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">React JS</p>
                    <p className="lead">Why 95? I set it at 95 as the majority of my Front End experience is with <strong>React</strong>!</p>
                    <small>Although I have been working with JS for over a year, at least half of that has been with <strong>React</strong>.</small>
                </div>

                <div className="skill-type flex-col">
                    <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={85} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">CSS</p>
                    <p className="lead">I set this at 85 simply due to not using enough of <strong>CSS</strong> to know it all!</p>
                    <small><strong>CSS</strong> is certainly something that I have things to learn about as there are soo many elements I haven't used yet!</small>
                </div>

                <div className="skill-type flex-col">
                    <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={90} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">HTML5</p>
                    <p className="lead">90 I feel is fair!</p>
                    <small>Due to mainly using React as my chosen framework, pure <strong>HTML5</strong> is something I don't use as much however I feel I'm more tha capable of creating a website with HTML only!</small>
                </div>
            </div>

            <p className="txt-md text-center mt-5">Back End Skills</p>
            <div className="skill-level-back mt-2">
                <div className="skill-type flex-col">
                <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={85} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">Node.JS</p>
                    <p className="lead">Why 85? I set this at 85 because I have recently completed 2 <strong>Full Stack</strong> Online stores and that has enabled me to widen my knowledge in Back End Development!</p>
                    <small>Over the past 3/4 months I have been using <strong>Node.js</strong> for the backend of the 2 online stores I mentioned earlier</small>
                </div>

                <div className="skill-type flex-col">
                <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={90} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">Express</p>
                    <p className="lead">Along with Node, I also used <strong>Express</strong> with the Back End of the websites</p>
                    <small><strong>Express</strong> has been something I've really enjoyed learning but as I am still relatively new to it I feel 90% is a fair skill rating!</small>
                </div>

                <div className="skill-type flex-col">
                <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={80} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">MySQL</p>
                    <p className="lead">For the Back End, I used the <strong>Node.js</strong> npm package for the database.</p>
                    <small>I have only used a small amount of <strong>MySQL</strong> in recent months but I have managed to get to grips with the standard syntax</small>
                </div>
            </div>
            <p className="txt-md text-center mt-5">Other Skills</p>
            <div className="skill-level-other mt-2">
                <div className="skill-type flex-col">
                <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={95} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">Bootstrap</p>
                    <p className="lead">Although I always use at least a little <strong>Bootstrap</strong> in my projects. There's bound to be elements from it that I'm yet to use</p>
                    <small>Bootstrap is a framework I use on all of my projects. Lately I have been using more of my own CSS however Bootstrap I find always comes in handy!</small>
                </div>

                <div className="skill-type flex-col">
                <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={95} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">jQuery</p>
                    <p className="lead">I use it a lot so I feel quite comfortable with it.</p>
                    <small>Just like <strong>Bootstrap</strong> I use <strong>jQuery</strong> in every project. I prefer to use it as I find it helps keep my code clean.</small>
                </div>

                <div className="skill-type flex-col">
                <CircleProgress 
                        primaryColor={["#cf6cc9", "#ee609c"]}
                        fontFamily="Orbitron" 
                        strokeWidth={13} 
                        percentage={95} 
                        secondaryColor="#f5f5f5"  
                        strokeLinecap="butt" />

                    <p className="txt-md font-0 txt-bold">Google's Firebase</p>
                    <p className="lead">I feel due to the fact I have used <strong>Firebase</strong> a lot in the past, 95% was the best skill rating</p>
                    <small>Before I began learning <strong>Node.js</strong> and <strong>Express</strong> I used <strong>Firebase</strong> to handle the backend and the database.</small>
                </div>
            </div>
        </div>
    )
}