import React, { useState, useRef, useEffect } from "react";
import ViewContent from "./Content/ViewContent";
import HomeBase from "./HomeBase";
import Skills from "./Skills";
import Contact from "./Contact";
import Portfolio from "./Portfolio";
const $ = require("jquery");

export default function Home() {
    const [content, setContent] = useState(<HomeBase />);
    useEffect(() => {
        $(".middle-side-bar ul li").on("click", eve => {
            setAsSelected(eve);
            const id = $(eve.target).attr("id");
            switch(id) {
                case "home":
                    setContent(<ViewContent content={<HomeBase />} />);
                break;
                case "contact":
                   setContent(<ViewContent content={<Contact />} />);
                break;
                case "skills":
                    setContent(<ViewContent content={<Skills />} />);
                break;
                case "portfolio":
                    setContent(<ViewContent content={<Portfolio />} />);
                break;
            }
        });
    }, []);

    const setAsSelected = eve => {
        $("li").removeClass("selected");
        $(eve.target).addClass("selected");
    }

    return (
        <div className="home-page shadow-sm">
            <div className="interactive-content">
            {content}
            </div>
        </div>
    )
}